export default {
	data() {
		return {
			msg:''
		};
	},
	methods:{
		submit(){
			console.log(this.msg)
			if(this.msg.trim()){
				let a=new Date().toLocaleString().split(' ')
				let date=a[0]//日期
				let time=a[1].slice(2,-3)//时间
				let self=this
				uni.getStorage({
					key:'user_phone',
					success(res) {
						if(!res.data){
							uni.showToast({
								title:'请先登录',
								icon:'none'
							})
						}else{
							// phone=res.data
							uni.request({
								url:'http://localhost:10086/chat/insert',
								method:'POST',
								data:{
									phone:res.data,
									msg:self.msg,
									date,
									time
								}
							}),
							uni.switchTab({
							    url: '../../pages/chat/chat'
							});
						}
						
					}
				})
				// console.log(time,now,phone)
			}else{
				uni.showToast({
					title:'请输入内容',
					icon:'none'
				})
			}
		}
	}
}
